body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Add your styles here */
.navbar { display: flex; justify-content: space-between; padding: 10px; background-color: #333; color: white; }
.navbar-left { font-size: 20px; }
.navbar-right button { padding: 10px; }
.login-modal { position: fixed; top: 0; left: 0; right: 0; bottom: 0; background-color: rgba(0, 0, 0, 0.5); display: flex; justify-content: center; align-items: center; }
.portal { display: flex; }
.sidebar { width: 200px; background-color: #f0f0f0; padding: 20px; }
.sidebar button { margin: 10px 0; }
